import React from "react";
import { useHistory } from "react-router-dom";

const NotFound = (props) => {
    const history = useHistory();

    return (
        <div style={{ margin: "auto" }}>
            <h1>  !</h1>
            <button onClick={() => {
                history.goBack();
            }}></button>
        </div>
    )
};

export default NotFound;